.brandLogo {
    width: auto;
    height: 3rem;
    /* height: 2.560vw; */
    pointer-events: none;
}
.header {
    position: fixed;
    top: 0;
    width: 100vw;
    /* height: 4.762vw; */
    height: 5.71428571rem;
    background: rgb(255,255,255);
    /* background: linear-gradient(180deg, rgba(255,255,255,1) 65%, rgba(255,255,255,0) 100%); */
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: "GMarket Normal";
    z-index: 98;
    /* font-size: 0.833vw; */
}

.container {
    /* width: calc(100vw - 210px); */
    width: 88vw;
    height: 100%;
    display: flex;
    align-items: center;
    /* background-color: orange; */
}

.menu {
    height: 100%;
    width: fit-content;
    display: flex;
    align-items: center;
}

.links {
    color: black;
    font-size: 1rem;
    width: fit-content;
    height: fit-content;

    transition: 0.5s;
}

.links:hover {
    transform: scale(1.1);
    /* text-shadow: 0px 1px 2px black; */
}

#linkOne {
    font-weight: lighter;
}

#linkTwo {
    font-weight: lighter;
    width: fit-content;
    text-align: center;
}

#linkThree {
    font-family: "GMarket Bold";
    font-weight: lighter;
}

.links::selection, .brandLogo::selection {
    background: transparent;
}

.linker {
    margin-left: 2rem;
    width: fit-content;
    height: fit-content;
    /* margin-left: 1.726vw; */
    display: flex;
    justify-content: center;
    align-items: center;
}

input:focus {
    outline: none;
}

@media (max-width: 414px) {
    .links {
        font-size: 11px;
    }
}

@media (max-width: 375px) {
    .header {
        height: 50px;
    }

    #linkTwo {
        width: 10rem;
    }

    .linker {
        margin-left: 1.3rem;
    }
}

@media (max-width: 320px) {
    .header {
        height: 50px;
    }

    #linkTwo {
        width: 9rem;
    }
}
